import React, { useState } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch } from 'react-redux';
import Model from '../../../components/UI/Model/Model';
import { payPaymentSaga } from '../../../store/actions';
import { TRANSACTION_ID_REQUIRED } from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const PayModal = props => {
  const { tableFilter, payoutId } = props;
  const [isDisable, setIsDisable] = useState(false);
  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
  };

  const dispatch = useDispatch();

  const validationSchema = Yup.object({
    transactionId: Yup.string().trim().required(TRANSACTION_ID_REQUIRED),
    notes: Yup.string().trim(),
  });

  const submitBtnHandler = values => {
    setIsDisable(true);
    dispatch(
      payPaymentSaga({
        ...values,
        payoutId,
        tableFilter,
        closeModel,
        setIsDisable,
      }),
    );
  };

  return (
    <Formik
      initialValues={{
        transactionId: '',
        notes: '',
      }}
      validationSchema={validationSchema}
      onSubmit={(values, { setSubmitting }) => {
        submitBtnHandler(values);
        setSubmitting(false);
      }}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        // isSubmitting,
      }) => (
        <Model
          closeModel={closeModel}
          modalId="modal-delete"
          headerTitle="Are you sure you want to pay?"
        >
          <form onSubmit={handleSubmit}>
            <div className="from-group text-left">
              <h4 className="required-field">Transaction Id:</h4>
              <input
                type="text"
                className="form-control"
                name="transactionId"
                value={values.transactionId}
                onChange={handleChange}
                onBlur={handleBlur}
              />
              <div className="error-message">
                {errors.transactionId &&
                  touched.transactionId &&
                  errors.transactionId}
              </div>
            </div>
            <div className="from-group text-left">
              <h4>Notes:</h4>
              <textarea
                className="form-control"
                style={{ height: '200px !important' }}
                name="notes"
                value={values.notes}
                onChange={handleChange}
                onBlur={handleBlur}
              />
            </div>
            <div className="clearfix m-t-md">
              <button
                type="submit"
                disabled={isDisable}
                className="btn btn-primary m-r-sm"
                data-toggle="modal"
                data-dismiss="modal"
                data-target="#modal-successb"
              >
                Yes
              </button>
              <button
                type="button"
                disabled={isDisable}
                className="btn btn-default"
                data-dismiss="modal"
                onClick={closeModel}
              >
                No
              </button>
            </div>
          </form>
        </Model>
      )}
    </Formik>
  );
};

export default PayModal;
